	module.exports = {"data":{"markdownRemark":{"html":"<h1>Using the Find Command</h1>\n<p>The Linux find command is a powerful tool to help you locate files and directories on your server. With a little practice, you can easily track things down based on name, type, size, or date (when they were created or last updated). </p>\n<p>Think of find as your eager helper:</p>\n<p>You: “I’m looking for something on my server.”</p>\n<p>Find: “I can help! What can you tell me about it?”</p>\n<p>You: “It was a file, bigger than 2GB, somewhere under my home directory, updated in the last 48 hours.”</p>\n<p>Find: “Tada!”</p>\n<p>Find is a program, so really you’d have to tell it <code>find ~ -type f -size +2G</code>.</p>\n<p>Here are some sample commands using find:</p>\n<ul>\n<li><code>find ~ -type d # Show me all the subdirectories inside my home directory</code></li>\n<li><code>find / -type f -name 'todo.txt' # Show me files named 'todo.txt' anywhere under the root directory (i.e. anywhere)</code></li>\n</ul>\n<p>The first parameter always names the directory in which we’ll look. In our examples above, these are ~ (home directory of the current user) and / (root directory of the filesystem).</p>\n<p>Other parameters are optional and can be combined in any ways you find useful:</p>\n<ul>\n<li>The type parameter allows you constrain the search for files only (f), directories only (d) or symbolic links (l). If you omit the type parameter, you’ll be searching for all of these types.</li>\n<li>The name parameter lets you specify what you want to find by name, either with a literal string (‘filename.txt’) or using wildcards (‘file?.*’).</li>\n</ul>\n<p><code>man find</code> will show you many more parameters, and is worth reviewing. Find can locate files by name, user, creation date, size and much more. Next time you’re looking for something, find it!</p>","fields":{"slug":"/linux/using-the-find-command/"},"frontmatter":{"title":"Using the Find Command"}}},"pathContext":{"slug":"/linux/using-the-find-command/"}}

